// SECTION iteration :: START

.iteration {
  overflow: hidden;
  width: 100%;
  height: 100vh;
  max-height: 100vh;

  &__slide {
    position: relative;
    width: 100%;
    min-height: 100%;
    padding: 50px 100px;

    &--1,
    &--7,
    &--14,
    &--16,
    &--19,
    &--21 {
      img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        display: block;
        max-width: 100%;
        height: auto;
        max-height: 100%;
      }
    }

    &--2,
    &--6,
    &--8,
    &--11,
    &--13,
    &--15,
    &--18,
    &--20,
    &--22 {
      p {
        position: absolute;
        top: 50%;
        left: 0;
        right: 0;
        transform: translateY(-50%);
        width: 100%;
        max-width: 898px;
        margin-left: auto;
        margin-right: auto;
        font-weight: bold;
        font-size: 52px;
        color: #9b9b9b;

        span {
          color: #000000;
        }
      }
    }

    &--3,
    &--4,
    &--5,
    &--9,
    &--10,
    &--12,
    &--17,
    &--23 {
      display: flex;
      align-items: center;
    }

    &--5 {
      .iteration {
        &__bg {
          &-path {
            top: 50%;
            right: -28%;
            transform: translateY(-50%);
            width: 1298px;
            height: 1298px;
            border-radius: 50%;
            background-color: #fafafa;
          }
        }
      }
    }

    &--6 {
      p {
        max-width: 1040px;
      }
    }

    &--8 {
      p {
        max-width: 760px;
      }
    }

    &--9,
    &--16 {
      .iteration {
        &__bg {
          &-path {
            top: 0;
            right: -55%;
            width: 1806px;
            height: 1020px;
            transform: skewX(-30deg);
            background-color: #f8fafc;
          }
        }
      }
    }

    &--10 {
      .iteration {
        &__bg {
          img {
            position: absolute;

            &:nth-of-type(1) {
              right: 220px;
              bottom: 250px;
            }

            &:nth-of-type(2) {
              right: -350px;
              bottom: -90px;
            }
          }
        }
      }
    }

    &--11 {
      p {
        max-width: 1040px;
      }
    }

    &--13,
    &--15,
    &--18,
    &--22 {
      p {
        max-width: 1315px;
      }
    }

    &--20 {
      p {
        max-width: 920px;
      }
    }
  }

  &__bg {
    z-index: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 100%;

    &-path {
      position: absolute;
    }
  }

  &__title {
    z-index: 0;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    font-weight: bold;
    font-size: 350px;
    line-height: 341px;
    color: #fafafa;
  }

  &__advantages {
    width: 100%;
    max-width: calc(100% / 3);

    &-wrapper {
      display: flex;
      justify-content: space-between;
      width: 100%;
      max-width: 1250px;
      margin-left: auto;
      margin-right: auto;
    }

    &-img {
      min-height: 220px;

      img {
        display: block;
        max-width: 100%;
        height: auto;
      }
    }

    &-text {
      width: 100%;
      max-width: 310px;
      padding-left: 35px;
      padding-top: 35px;
    }
  }

  &__search {
    z-index: 2;
    position: relative;
    width: 100%;
    max-width: 710px;
    margin-left: auto;
    margin-right: auto;

    &-title {
      width: 100%;
      max-width: 525px;
      padding-bottom: 90px;
      font-weight: bold;
      font-size: 26px;
      color: #000;
    }

    &-img {
      padding-bottom: 50px;

      img {
        display: block;
        max-width: 100%;
        height: auto;
        margin-left: auto;
      }
    }

    &-desc {
      padding-left: 80px;
      font-weight: normal;
      font-size: 20px;
      color: #848484;
    }
  }

  &__ai {
    z-index: 2;
    position: relative;
    width: 100%;
    max-width: 710px;
    margin-left: auto;
    margin-right: auto;

    &-title {
      width: 100%;
      max-width: 525px;
      padding-bottom: 90px;
      font-weight: bold;
      font-size: 26px;
      color: #000;
    }

    &-badge {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      width: 100%;
      max-width: 630px;
      margin-left: auto;
      margin-right: auto;
      padding-bottom: 60px;

      a {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 151px;
        height: 40px;
        margin-left: 10px;
        margin-right: 10px;
        margin-bottom: 10px;
        padding: 0 20px;
        border-radius: 8px;
        background-color: #00449d;

        p {
          position: relative;
          top: -1px;
          font-family: 'Open Sans', sans-serif;
          padding-right: 20px;
          line-height: 1;
          font-size: 18px;
          color: #fff;
        }

        svg {
          width: 18px;
          height: 18px;
        }
      }
    }

    &-desc {
      padding-left: 80px;
      font-weight: normal;
      font-size: 20px;
      color: #848484;
    }
  }

  &__column {
    z-index: 2;
    position: relative;
    width: 100%;
    max-width: 610px;
    margin-left: auto;
    margin-right: auto;

    &-img {
      padding-bottom: 50px;

      img {
        display: block;
        max-width: 100%;
        height: auto;
      }
    }

    &-text {
      padding-left: 100px;
      font-weight: normal;
      font-size: 20px;
      color: #848484;
    }
  }

  &__pipelines {
    z-index: 2;
    position: relative;
    width: 100%;
    max-width: 840px;
    margin-left: auto;
    margin-right: auto;

    &-title {
      width: 100%;
      max-width: 525px;
      padding-bottom: 40px;
      font-weight: bold;
      font-size: 26px;
      color: #000;
    }

    &-img {
      padding-bottom: 20px;

      img {
        display: block;
        max-width: 100%;
        height: auto;
      }
    }

    &-desc {
      padding-left: 160px;
      font-weight: normal;
      font-size: 20px;
      color: #848484;
    }
  }

  &__contacts {
    z-index: 2;
    position: relative;
    width: 100%;
    max-width: 920px;
    margin-left: auto;
    margin-right: auto;

    &-title {
      padding-left: 115px;
      padding-bottom: 25px;
      font-weight: bold;
      font-size: 30px;
      color: #000;
    }

    &-img {
      img {
        display: block;
        max-width: 100%;
        height: auto;
      }
    }
  }

  &__schedule {
    z-index: 2;
    position: relative;
    width: 100%;
    max-width: 630px;
    margin-left: auto;
    margin-right: auto;

    &-img {
      img {
        display: block;
        max-width: 100%;
        height: auto;
      }
    }

    &-desc {
      padding-left: 120px;
      font-weight: normal;
      font-size: 20px;
      color: #848484;
    }
  }

  &__data {
    z-index: 2;
    position: relative;
    width: 100%;
    max-width: 740px;
    margin-left: auto;
    margin-right: auto;

    &-title {
      width: 100%;
      max-width: 450px;
      padding-bottom: 80px;
      font-weight: normal;
      font-size: 20px;
    }

    &-img {
      padding-left: 50px;
      
      img {
        display: block;
        max-width: 100%;
        height: auto;
      }
    }
  }

  .swiper {
    &-container {
      opacity: 0;
      width: 100%;
      height: 100%;
      padding: 0;
    }
  }
}

// SECTION iteration :: END